import React, { useState } from 'react';
import style from './form.module.css'; //   
import back from './back.png';
import { useMask } from '@react-input/mask';

const Form: React.FC = () => {
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [agree, setAgree] = useState(false);
    const placeholder = '+7 (___) ___-__-__';
    const inputRef = useMask({ mask: "8 (___) ___-__-__", replacement: { _: /\d/ } });


    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();

        if (agree) {
            const formEl = document.querySelector('form')
            // @ts-ignore
            const formData = new FormData(formEl)
            console.log(phone)

            fetch('https://script.google.com/macros/s/AKfycbzeoEN7-b4iYhQoCYRghXCzyzACXbthL56dnKOW-HtmcELBjKh3LXkqgY4WBBu91P9C/exec', {
                method: 'POST',
                body: formData,
            })
                .then(response => {
                    setName('')
                    setPhone('')
                    setAgree(false)
                })
                .catch(error => console.error('      Google ', error));

        } else {
            alert('        .');
        }
    };

    return (
        <div className={style.formContainer} style={{ backgroundImage: `url(${back})`}} id="myElement">
            <div>
                <h2 className={style.title}> </h2>
                <p className={style.text}>     10 ,      ,        .               .   ,   !</p>
            </div>

            <form onSubmit={handleSubmit} className={style.form}>
                <div className={style.formRow}>
                    <input placeholder='' className={style.input} name="Name" type="text"  value={name} onChange={(e) => setName(e.target.value)} required/>
                </div>
                <div className={style.formRow}>
                    <input ref={inputRef} value={phone} placeholder="" className={style.input} name="Phone"  title="     +7 (XXX) XXX-XX-XX" onChange={(e) => setPhone(e.target.value)} required/>
                </div>
                <div className={style.formRow}>
                    <input type="checkbox" checked={agree} onChange={(e) => setAgree(e.target.checked)} />
                    <label>    </label>
                </div>

                <button className={style.button} type="submit"></button>
            </form>
        </div>
    );
};

export default Form;